

function addAreaAsync() {
  return dispatch => {
    setTimeout(() => {
      // Yay! Can invoke sync or async actions with `dispatch`
      dispatch(increment());
    }, 1000);
  };
}

export function addArea(area){
    return {
        type: "ADD_AREA",
        payload: area
    }
}



